body {
    font-family: 'Roboto', sans-serif;
    margin: 0px;
    overflow-x: hidden;
    background-color: #c9cdd1
  }
  
  p {
    margin-block-start: 0px;
    margin-block-end: 5px;
  }
  ul {
    margin-block-start: 0px;
  }
   
  .page-wrapper {
    text-align: start;
    margin: 10px;
  }
  /* #c5c5c5 #335c81 #829191 #84a9c0 #279af1 */
  .title {
    font-size: 35px;
  }
  .sub-title {
    text-align: center;
    font-size: 20px;
    margin: 0px 0px 10px 0px;
  }
  
  .topbar-wrapper {
    background-color: white;
  }
  .topbar {
    padding-bottom: 5px;
    padding-left: 5px;
    padding-top: 5px;
    max-width: 800px;
    margin: auto;
    justify-content: space-between;
    background-color: white
  }
  
  
  .flex {
    display: flex;
  }
  
  .topbar-buttons {
    margin-left: 50px;
  }
  
  .topbar-buttons:first-child {
    border-radius: 8px;
  }
  
  .topbar-link {
    padding: 12px 10px;
    cursor: pointer;
    text-align: center;
    vertical-align: middle;
    height: fit-content;
    margin-top: auto;
    margin-bottom: auto;
    background-color: white;
  }
  

  
  .topbar-link:hover {
    background-color: rgb(92, 92, 92);
    background-color: #335C81;
    color:white
  
  }
  
  .topbar-link-instalogo:hover {
    background-color: rgb(241, 241, 241);
  }

  .sidebar-item:hover {
    background-color: #d3d3d3;
    cursor: pointer;
  }
  
  
  .front-picture {
    width: 99vw;
    height: auto;
  }
  
  .location-text {
    position: absolute;
    transform: translate(10px, -40px);
    color: white;
  
  
  }
  
  .content {
    max-width: 800px;
    align-content: center;
    /* margin-top: 20px; */
    margin-left: auto;
    margin-right: auto;
  }
  .container {
    margin: 20px 0px 20px 0px;
  }
  
  .backtotop-link {
    font-size: 15px;
    padding: 10px 20px;
    cursor: pointer;
    text-align: center;
    vertical-align: middle;
    height: fit-content;
    margin-top: auto;
    margin-bottom: auto;
    background-color: #e4e4e4;
    color: black;
    border-radius: 8px;
  
  }
  
  .backtotop-link:hover {
    background-color: rgb(92, 92, 92);
  
  }
  
  .container-title-wrapper {
    width: 100%;
    color: white;
    background-color: #335C81;
    padding: 10px 0px 10px 0px;
    
  }
  
  .container-title {
    max-width: 800px;
    margin-left: auto;
    margin-right: auto;
    font-size: 40px;
    justify-content: space-between;
    display: flex;
  
  
  }
  
  .about-title {
     /* font-family: "Roboto script=all rev=1";  */
     /* font-weight: 900; */
      /* font-style: normal; */
    font-weight: bold;
    font-size: 23px;
    /* background-color: #84a9c0; */
    border-radius: 8px;
    padding: 5px;
    margin: 5px;
    width: fit-content;
    color: rgb(36, 36, 36);
  
  }
  
  .aboutme-wrapper {
  }
  
  .aboutme {
    background-color: white;
    border-radius: 8px;
    padding: 5px;
    margin: 5px;
  
  }
  
  .project {
    background-color: white;
    border-radius: 8px;
    margin: 5px;
  }
  .project-wrapper {
    /* height: 200px; */
    background-size: cover;
    /* -webkit-filter: blur(4px); */
    margin-bottom: 10px;
    background-color: white;
    border-radius: 8px;
    margin: 5px;
    /* border: 1px solid black; */
    
  }
  
  .project-title {
    padding: 5px 5px;
    border-radius: 8px;
    font-size: 25px;
    display: flex;
    justify-content: space-between;
    /* margin-bottom: 5px; */
    /* font-weight: bold; */
    /* background-color: #84a9c0; */
    /* #c5c5c5 #335c81 #829191 #84a9c0 #279af1 */
    color: rgb(36, 36, 36);
  }
  
  .project-title-only {
    font-weight: bold;
  
  }
  
  .project-image-wrapper {
  
  }
  
  .project-link {
    /* cursor: pointer; */
    padding: 5px;
    border-radius: 8px;
    /* color: -webkit-link; */
    cursor: pointer;
    /* text-decoration: underline; */
    background-color: #e2e2e2;
  
  
  }
  
  .project-description {
    display: flex;
    justify-content: space-between;
    padding: 5px;
    padding-top: 0px;
  }
  
  .project-link:hover {
    transform: translate(1%, 1%);
    /* background-color: lightblue; */
    background-color: #335C81;
  
  }
  .project-image {
    max-width: 400px;
  
  }
  .td {
    padding: 0px;
  }
  
  .sub-text {
    font-size: 18px;
    text-align: center
  }
  .icon {
    width: 25px;
    height: 25px
  }
  .icon-mongo {
    width: 68px;
    height: 20px
  }
  .icon-azure {
    width: 63px;
    height: 20px
  }
  .biography {
    /* margin: 5px; */
  }
  .biography-details {
    margin: 0px 0px 0px 5px;
  }
  .section-title {
    margin: 20px 0px 0px 0px;
    font-size: 25px
  }
  ul {
    margin-block-start: 5px
  }